<template>
  <div id="center">
    <h2>2024级10班新年礼物抽奖</h2>
    <div id="tanyu">
    <el-button type="primary" @click="jkl">随机生成列表</el-button>
    <el-button type="primary" @click="tanyu">停止</el-button>
    <el-button type="primary" @click="jkl2">随机生成一个数</el-button>
    <el-button type="primary" @click="tanyu2">停止</el-button>

  <div><h3>抽取结果：{{nub}}</h3></div>
    </div>
    <div id="data">
      <el-table :data="tableData" border stripe style="width: 100%">
        <el-table-column prop="date" label="礼物编号" width="180">
        </el-table-column>
        <el-table-column prop="name" label="序号" width="180">
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
// import {Table,TableColumn } from 'element-ui';
export default {
  data() {
    return {
      tableData: [],
      b:'',
      nub:'',
      k:''
    
    };
  },
  
  methods: {
    //   open4() {
    //     this.$message.error('错了哦，这是一条错误消息');
    //   },
    jkl() {
      function randomsort(a, b) {
        return Math.random() > 0.5 ? -1 : 1; //用Math.random()函数生成0~1之间的随机数与0.5比较，返回-1或1
      }
      var arr = new Array();

      for (var i = 1; i <= 45; i++) {
        arr[i] = i
      }
      this.b=setInterval(()=>{
              arr.sort(randomsort);
      for (var i = 0; i < this.tableData.length; i++) {
        this.tableData[i].name = arr[i];
      }
      },100)

      console.log(this.b)

    },tanyu2(){
      clearInterval(this.k);
    },
    jkl2(){
      var x = 1,y = 45;
      
      this.k=setInterval(()=>{
   this.nub=Math.round(Math.random()*(y-x)+x)
      },100)
    },
    tanyu(){
clearInterval(this.b);
console.log(this.b)
    }

  },
  mounted() {
    for (var i = 1; i <= 45; i++) {
      let a = {
        date: i,
        name: "?",
      };
      this.tableData.push(a);
    }

    // console.log(this.tableData);
    console.log(this.tableData);
  },
};
</script>

<style>
#center {
  /* text-align: center; */
  width: 600px;
  /* height:200px; */
  margin: 0 auto;
  margin-top: 10px;
  /* background-color: blacck; */
}#tanyu{
  margin: 20px;
}
</style>